.container {
  background-color: $mainColor;
  background-image: url("../img/plate-desktop-mask@1x.png");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/plate-desktop-mask@2x.png");
  }

  padding-bottom: 17px;
}

.container--half-opacity {
  opacity: 0.5;
}

@media (max-width: $tabletResolution) {
  .container {
    padding-bottom: 0;
  }
}
